'use strict';

var React = require('react/addons');
var assign = require(17);
var shortid = require(25);

var onlyChild = React.Children.only;
var findDOMNode = React.findDOMNode;

var CSSCore = require(18);
var ReactTransitionEvents = require(20);

var TICK = 17;
var MARGIN_NAMES = ['marginTop', 'marginRight', 'marginBottom', 'marginLeft'];
var PADDING_NAMES = ['paddingTop', 'paddingRight', 'paddingBottom', 'paddingLeft'];

var FIX_ELEMENT_SIZE = 1;
var HIDE_ELEMENT = 2;

function fixElementSize(sheet, node, selector) {
    var computedStyle = window.getComputedStyle(node, null);
    var margin = MARGIN_NAMES.map(function (marginName) {
        return computedStyle[marginName];
    }).join(' ');
    var padding = PADDING_NAMES.map(function (paddingName) {
        return computedStyle[paddingName];
    }).join(' ');
    sheet.insertRule(selector + '{height:' + computedStyle.height + ';margin:' + margin + ';padding:' + padding + ';}', FIX_ELEMENT_SIZE);
}

var CatfootedTransitionGroupChild = React.createClass({
    displayName: 'CatfootTransitionGroupChild',

    componentWillMount: function () {
        var cssId = '_catfoot_' + shortid.generate();
        var name = this.props.name;

        var style = document.createElement('style');
        style.appendChild(document.createTextNode(''));
        document.head.appendChild(style);

        var sheet = style.sheet;
        sheet.insertRule('#' + cssId + '.' + name + '-appear,#' + cssId + '.' + name + '-enter,#' + cssId + '.' + name + '-leave.' + name + '-leave-active{height:0;margin:0;padding:0;}', 0);

        this.cssId = cssId;
        this.style = style;
        this.sheet = sheet;
    },

    componentDidMount: function () {
        var node = findDOMNode(this);
        node.id = this.cssId;
    },

    componentWillUnmount: function () {
        document.head.removeChild(this.style);
    },

    _willShow: function (action, done) {
        var cssId = this.cssId;
        var name = this.props.name;
        var className = name + '-' + action;
        var activeClassName = className + '-active';

        var sheet = this.sheet;
        var node = findDOMNode(this);

        fixElementSize(sheet, node, '#' + cssId + '.' + activeClassName);
        sheet.insertRule('#' + cssId + '{display:none;}', HIDE_ELEMENT);

        window.setTimeout(function () {
            CSSCore.addClass(node, className);
            sheet.deleteRule(HIDE_ELEMENT);

            var endEventListener = function () {
                done();
                ReactTransitionEvents.removeEndEventListener(node, endEventListener);
            };

            ReactTransitionEvents.addEndEventListener(node, endEventListener);
            window.setTimeout(function () {
                CSSCore.addClass(node, activeClassName);
            }, TICK);

        }, TICK);
    },

    _didShow: function (action) {
        var name = this.props.name;
        var node = findDOMNode(this);
        var className = name + '-' + action;
        var activeClassName = className + '-active';
        CSSCore.removeClass(node, className);
        CSSCore.removeClass(node, activeClassName);
        this.sheet.deleteRule(FIX_ELEMENT_SIZE);
    },

    componentWillAppear: function (done) {
        if (this.props.appear) {
            this._willShow('appear', done);
        } else {
            done();
        }
    },

    componentDidAppear: function () {
        if (this.props.appear) {
            this._didShow('appear');
        }
    },

    componentWillEnter: function (done) {
        if (this.props.enter) {
            this._willShow('enter', done);
        } else {
            done();
        }
    },

    componentDidEnter: function () {
        if (this.props.enter) {
            this._didShow('enter');
        }
    },

    componentWillLeave: function (done) {
        var cssId = this.cssId;
        var name = this.props.name;
        var className = name + '-leave';
        var activeClassName = className + '-active';

        var node = findDOMNode(this);
        var sheet = this.sheet;
        fixElementSize(sheet, node, '#' + cssId + '.' + className);

        window.setTimeout(function () {
            CSSCore.addClass(node, className);

            var endEventListener = function () {
                done();
                ReactTransitionEvents.removeEndEventListener(node, endEventListener);
            };

            ReactTransitionEvents.addEndEventListener(node, endEventListener);
            window.setTimeout(function () {
                CSSCore.addClass(node, activeClassName);
            }, TICK);

        }, TICK);
    },

    render: function () {
        return onlyChild(this.props.children);
    }
});

module.exports = CatfootedTransitionGroupChild
