'use strict';

var React = require('react/addons');
var assign = require(17);

var ReactTransitionGroup = React.createFactory(
    React.addons.TransitionGroup
);
var CatfootedTransitionGroupChild = React.createFactory(
    require(16)
);

var CatfootedTransitionGroup = React.createClass({
    displayName: 'CatfootTransitionGroup',

    propTypes: {
        transitionName: React.PropTypes.string.isRequired,
        transitionAppear: React.PropTypes.bool,
        transitionEnter: React.PropTypes.bool,
        transitionLeave: React.PropTypes.bool
    },

    getDefaultProps: function () {
        return {
            transitionAppear: false,
            transitionEnter: true,
            transitionLeave: true
        };
    },

    _wrapChild: function (child) {
        return CatfootedTransitionGroupChild(
            {
                name: this.props.transitionName,
                appear: this.props.transitionAppear,
                enter: this.props.transitionEnter,
                leave: this.props.transitionLeave
            },
            child
        );
    },

    render: function () {
        return (
            ReactTransitionGroup(
                assign({}, this.props, { childFactory: this._wrapChild })
            )
        );
    }
});

module.exports = CatfootedTransitionGroup;
